.login {
  transition: transform 0.5s ease-in-out;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.login .block1 {
  background-color: #ffffff;
  display: inline-flex;
  width: 325px;
  height: 540px;
  position: relative;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.login .content-block {
  display: inline-flex;
  background-color: #ffffff;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.login .note {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  position: relative;
}

.login .text-wrapper {
  position: relative;
  width: fit-content;
  margin-top: -1px;
  font-family: "Poppins", Helvetica;
  font-weight: 700;
  color: #2f3367;
  font-size: 28px;
  letter-spacing: 0;
  line-height: normal;
}

.login .note-text {
  position: relative;
  width: fit-content;
  font-family: "Poppins", Helvetica;
  font-weight: 500;
  color: #303468;
  font-size: 16px;
  letter-spacing: 0;
  line-height: normal;
}


.login .top-block {
  display: inline-flex;
  align-items: center;
  position: relative;
  height: 40px;
}

.login .center-block {
  display: inline-flex;
  width: 200px;
  height: 40px;
  align-items: center;
}

.login .email-icon {
  position: absolute;
  vertical-align: middle;
  left: 5px;
}

.login .email-input {
  padding-left: 30px;
  width: 200px;
  height: 40px;
  background-color: #f5f5f7;
  border-radius: 10px;
  outline: none;
  border-style: none;
  color: #000;
  font-family: "Poppins", Helvetica;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
}

.login .email-input::placeholder {
  position: relative;
  width: fit-content;
  margin-top: -1px;
  font-family: "Poppins", Helvetica;
  font-weight: 500;
  color: #8b8fa8;
  font-size: 16px;
  letter-spacing: 0;
  line-height: normal;
}
.login .verify-input {
  width: 325px;
  height: 40px;
  background-color: #f5f5f7;
  border-radius: 10px;
  outline: none;
  border-style: none;
  color: #000;
  font-family: "Poppins", Helvetica;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
}

.login .verify-input::placeholder {
  position: relative;
  width: fit-content;
  margin-top: -1px;
  font-family: "Poppins", Helvetica;
  font-weight: 500;
  color: #8b8fa8;
  font-size: 16px;
  letter-spacing: 0;
  line-height: normal;
}

.login .email-text {
  display: flex;
  width: 125px;
  height: 40px;
  justify-content: center;
  color: #000;
  font-family: "Poppins", Helvetica;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  flex-direction: column;
}


.login .email-tag {
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: 20px;
  left: 32px;
}


.login .bottom-block {
  display: inline-flex;
  width: 100%;
  position: relative;
  justify-content: flex-end;
}

.login .bottom-btn {
  display: flex;
  height: 50px;
  width: 120px;
  align-items: center;
  padding: 18.5px;
  position: relative;
  background-color: #007dfa;
  border-radius: 8px;
  box-sizing: border-box;
  border: none;
  outline: none;
}

.login .text-wrapper-2 {
  position: relative;
  width: fit-content;
  margin-top: -1px;
  font-family: "Poppins", Helvetica;
  font-weight: 600;
  color: #ffffff;
  font-size: 22px;
  letter-spacing: 0;
  line-height: normal;
}

.login .chevron-right {
  position: relative;
  width: 33px;
  height: 33px;
}